.section-calendar {
    border: 1px solid $border-color;
    border-radius: $border-radius;
    ul {
        list-style-type: none;
    }
    .month {
        padding: 1px 35px;
        color: $nav-a-color;
        width: 100%;
        background: $nav-panel-color;
        border-bottom: 1px solid $border-color;

        li {
            text-transform: uppercase;
            letter-spacing: 3px;
            text-align: center;
            position: relative;
            color: $nav-a-color;
        }
        .prev {
            margin-left: -40px;
        }

        .prev,
        .next {
            color: $search-color;
            border-radius: $border-radius-round;
            border: 2px solid $border-color;
            padding: 7px;
        }
        .month-year {
            font-size: 1.5rem;
            font-weight: bold;
            letter-spacing: 5px;
        }
    }
}


/* Weekdays (Mon-Sun) */
.weekdays {
    margin: 0;
    padding: 18px 15px;
    background-color: $primary-white;
    border-bottom: 1px solid $border-color;
    li {
        display: inline-block;
        width: 13.6%;
        font-size: 1.4rem;
        font-weight: bold;
        color: $nav-a-color;
        text-align: center;
        text-transform: uppercase;
    }
}


/* Days (1-31) */
.days {
    padding: 20px 25px 15px;
    background: $primary-white;
    margin: 0;


    li {
        list-style-type: none;
        display: block;
        float: left;
        width: 52px;
        height: 52px;
        line-height: 50px;
        text-align: center;
        background: $primary-white;
        margin-right: 9px;
        margin-bottom: 12px;
        font-size: 1.5rem;
        font-weight: bold;
        border: 3px solid $search-color;
        border-radius: $border-radius-round;
        color: $nav-a-color;
        cursor: pointer;
        position: relative;
        z-index: 100;

        &:empty {
            background: $search-color;

        }
    }

    .active.start {

        @include box-shadow-hack;
    }

    .active-inside {
        color: $primary-white;
        background: $primary-color-red;
        border-color: $primary-color-red;
        border-radius: 0;

        &:after {
            content: '';
            position: absolute;
            left: 100%;
            top: -3px;
            z-index: -100;
            width: 75%;
            height: 100%;
            background: $primary-color-red;
            border: 3px solid $primary-color-red;
        }
    }


    li:nth-of-type(7n + 1) {
        border-radius:  100%;

        &.active-inside:after {
            left: 50%;
        }
    }

    li:nth-of-type(7n) {
        margin-right: 0;
        border-radius: 100%;

        &.active-inside:after {
            display: none;
        }
    }

    .active {
        @include red-border-color;
    }
}